<template>
	<view class="home"> 
	   <!-- 轮播图 -->
	   <swiper indicator-dots circular autoplay interval="3000">
		   <swiper-item v-for="(item,i) in swipers" :key="item.goods_id">
			  <image :src="item.image_src"></image>
		   </swiper-item>
	   </swiper>
	   <!-- 导航区域 -->
	   <view class="nav" v-show="isShow">
            <image :src="category" class="category" @click="cateGory"></image>
			<image :src="seckill"></image>
			<image :src="supermarket"></image>
			<image :src="maternal"></image>
	   </view>
	   <!-- 楼层 -->
	   <view class="floor">
	      <!-- 户外活动 -->
		  <view class="womanFashion" v-for="(item, index1) in outDoor" :key="item.name">
              <view class="header_woman">
			  	  <image mode="widthFix" :src="item.floor_title.image_src"></image>
			  </view>
			  <view class="detail" style="width: 98%; margin: auto;">			 
				  <view class="detail_img" v-for="(list, index2) in item.product_list" :key="list.name">
					  <image mode="index2===1?'widthFix':'scaleToFill'" :src="list.image_src" @click="btn(index2)"></image>
				  </view>  
			  </view>
		  </view>
	   </view>
	   <!-- 一键触顶 -->
	   <view class="top-button" @click="ToTop" :style="{'display':(topState===true? 'block':'none')}">
		   <image src="../../static/top.png"></image>
	   </view>
	</view>
</template>

<script>
	import exDrawer from '@/components/ex-drawer/ex-drawer.vue'
	export default {
		components:{
		exDrawer
		},
		data() {
			return {
			  swipers: [],
			  category: '', // 分类	  
              seckill: '',  // 秒杀拍
			  supermarket: '', // 超市购
			  maternal: ''  ,// 母婴品
			  outDoor: [],
			  topState:false,  // 页面触顶
			  // 页面刷新
			  ifOnShow: false,
			  isShow: true
			}
		},
		methods: {
			// 轮播图
			async getSwiper() {
				const res = await this.$myRequest({
					url: '/home/swiperdata',
				})
				this.swipers = res.data.message
				// console.log(res)
			},
			// 导航区域
			async getNav() {
				const res = await this.$myRequest({
					url: '/home/catitems'
				})
				const a = res.data.message
			    this.category = a[0].image_src 
				this.seckill = a[1].image_src
				this.supermarket = a[2].image_src
				this.maternal = a[3].image_src
			},
		    // 楼层
			async getFloor() {
				const res = await this.$myRequest({
					url: '/home/floordata'
				})
				this.outDoor = res.data.message		
			},
			btn(index2) {
				console.log(index2)
			},
			// 一键触顶
			ToTop() {
			  uni.pageScrollTo({
			    scrollTop: 0,
			    duration: 300
			  })
			},
			// 分类跳转
			cateGory() {
				uni.switchTab({
					url: '../classify/classify'
				})
			},
		    onPageScroll(e){ //根据距离顶部距离是否显示回到顶部按钮
		      if(e.scrollTop>600){ //当距离大于600时显示回到顶部按钮
		        this.topState = true
		      }else{ //当距离小于600时显示回到顶部按钮
		        this.topState = false
		    }
		},
		// 下拉刷新
		onPullDownRefresh() {
			// #ifndef H5
			setTimeout(() => {
				this.getFloor();
				this.getNav();
				this.getSwiper();
    			uni.stopPullDownRefresh();
			}, 1500);
			// #endif
			// #ifdef H5
			  setTimeout(() => {
				uni.stopPullDownRefresh();
				location.reload()  
			  },1000)
			// #endif
		},
		onLoad() {
			// 轮播图
			this.getSwiper()
			// 导航栏
			this.getNav()
			// 楼层
			this.getFloor()
		},
		
		// 导航栏按钮事件
		onNavigationBarButtonTap(val) {
			// 登录事件
			if (val.text === '登录') {
				uni.navigateTo({
					url: '../login/login'
				})
			} else {
				console.log('三')
			}
		},
		// 点击输入框跳转页面
		onNavigationBarSearchInputClicked () {
			uni.navigateTo({
				url: '../search/search'
			})
		},		
		onShow() {
			// #ifdef H5
		  //   if(this.ifOnShow){
		  //     setTimeout(() => {
				// location.reload()  
			 //  },0.1)
		  //   }
			// #endif
		},
		onHide(){
			// #ifdef H5
		 //    this.ifOnShow = true
			// this.swipers = ''
			// this.isShow = false
			// this.outDoor = ''
			// #endif
		}
		}
	}
</script>

<style lang="less">
	@border: 1px red solid;
	.home {
		width: 100%;
		// 导航轮播
		swiper {
			width: 100%;
			height: 400rpx;
			image {
				width: 100%;
				height: 100%;
			}
		}
		// 导航区域
		.nav {
			width: 100%;
			margin-top: 40rpx;
			margin-bottom: 60rpx;
			display: flex;
			image {
				height: 200rpx;
			}
		}
	    // 时尚女装
		.womanFashion {	  
		  .header_woman {	 
			  padding: 10rpx 0;
			  image {
				  width: 100%;
			  }
		  }
		  .detail {
			overflow: hidden;
			.detail_img {		  
			  float: left;
			  width: 33.33%;
			  // 对后四张图片操作
			  :nth-last-child(-n+5) {
				 margin-left: 10rpx;
			  }
			  &:nth-child(1){
			      height: 57vw;
				  margin-left: -15rpx;
			  }
			  &:nth-child(2) {
				  border-bottom: 10rpx solid #fff;
				  height: 33.33vw * 386 / 232 / 2; 		  
			  }
			  &:nth-child(3){
				  border-bottom: 10rpx solid #fff;
				  height: 33.33vw * 386 / 232 / 2;
			  }
			  &:nth-child(4) {
				  border-bottom: 10rpx solid #fff;
				  height: 33.33vw * 386 / 232 / 2;
			  }
			  &:nth-child(5) {
				  height: 33.33vw * 386 / 232 / 2;
			  }
			  image {
				 width: 100%;
				 height: 100%;
			  }
			 }
			}
		}
	    // 一键触顶
		.top-button {
	       position: fixed;
	       right: 23rpx;
	       bottom: 150rpx;
	       z-index: 9999;
		   image {
			   width: 90rpx;
			   height: 90rpx;
		   }
	    }
	}
    @media screen and (min-width: 1024px){
    	.home {
    		width: 100%;
    		// 导航轮播
    		swiper {
    			width: 100%;
    			height: 900rpx;
    			image {
    				width: 100%;
    				height: 100%;
    			}
    		}
    		// 导航区域
    		.nav {
    			width: 100%;
    			margin-top: 55rpx;
    			display: flex;
    			justify-content: space-around;
    			image {
    				height: 570rpx;
    			}
    		}
    	    // 楼层
    		.floor {
    			width: 100%;
				margin-top: 130rpx;	
				// 时尚女装
				.womanFashion {	  
				  .header_woman {	 
					  // padding: 40rpx 0 20rpx 0;
					  image {
						  width: 100%;
					  }
				  }
				  .detail {
					overflow: hidden;
					.detail_img {		  
					  float: left;
					  width: 33.33%;
					  // 对后四张图片操作
					  :nth-last-child(-n+4) {
					     // height: 33.33vw * 386 / 232 / 2;
						 margin-left: 10rpx;
					  }
					  &:nth-child(1){
					     height: 55.8vw;
					  }
					  &:nth-child(2) {
						  border-bottom: 10rpx solid #fff;
						  height: 33.33vw * 386 / 232 / 2; 
						  
					  }
					  &:nth-child(3){
						  border-bottom: 10rpx solid #fff;
						  height: 33.33vw * 386 / 232 / 2;
					  }
					  &:nth-child(4) {
						  border-bottom: 10rpx solid #fff;
						  height: 33.33vw * 386 / 232 / 2;
					  }
					  &:nth-child(5) {
						  height: 33.33vw * 386 / 232 / 2;
					  }
					  image {
						 width: 100%;
						 height: 100%;
					  }
					 }
					}
				}
    	}
       }
	}
</style>

